* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family:Verdana, Geneva, Tahoma, sans-serif;
}
#root{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.App{
    width: 800px;

}

.post {
    display: flex;
    padding: 15px;
    border: 2px solid teal;
    margin-top: 15px;
    justify-content: space-between;
    align-items: center;
}

.post__btns{
    display: flex;
}

.post-enter {
    transform: translateX(-150px);
}
.post-enter-active {
transform: translateX(0);
  opacity: 1;
  transition: all .150s ease-in;
}
.post-exit {
  opacity: 1;
}
.post-exit-active {
    opacity: 0;
    transform: translateX(-150px);
    transition: all .150s ease-in;
}

.page__wrapper{
    margin-top: 30px;
    display: flex;
    justify-content: center;
}

.page{
    padding: 10px;
    border: 1px solid teal;
    cursor: pointer;
    font-size: 16px;

}



.page + .page{
    margin-left: 15px;
}
.page__current{
    border: 2px solid crimson;
    font-weight: 700;
}

.navbar{
    height: 50px;
    width: 100vw;
    display: flex;
    align-items: center;
    padding: 0 15px;
    background-color:lightgreen;
}

.navbar__links{
    margin-left: auto;

}

.navbar__links a {
    text-decoration: none;
    color: teal;
    font-size: 23px;
}

.navbar__links a + a{
    margin-left: 10px;
}

.navbar__links a.active{
    color: crimson;
}